import Vue from "vue";
import App from "./App.vue";

import VueRouter from "vue-router";

import Home from "./components/HelloWorld.vue";

Vue.config.productionTip = false;

Vue.prototype.$toAppLink = function (param) {
  window.onload = function () {
    window.location = `demo://test-link/${param}`;
    setTimeout(function () {
      window.location =
        "https://play.google.com/store/apps/details?id=com.cathay.main&hl=zh_TW&gl=US";
    }, 500);
  };
};

Vue.use(VueRouter);
const routes = [{ path: "/" }];

const router = new VueRouter({
  mode: "hash",
  routes: routes,
});

new Vue({
  router,
  render: (h) => h(App),
}).$mount("#app");
