<template>
  <div id="app">
    <div>{{ link }}</div>
    <!-- <router-view></router-view> -->
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      link: "",
    };
  },
  created() {
    const url = new URL(window.location.href);
    const link = `${window.location.href}`.replace(
      `${url.protocol}//${url.host}/#/`,
      ""
    );
    if (link) {
      this.link = `link: ${link}`;
      this.$toAppLink(link);
    } else {
      this.link = `no link`;
    }
  },
  mounted() {},
};
</script>

